body{
    background-color: #000000d9 !important;
    color: white !important;
    font-family: Poppins, Nunito Sans, Montserrat, sans-serif;
}

img{
    width: 100%;
}
#main{
    background-color: #0a3872;
}

.navbar-nav{
    width: 100%;
    justify-content: center;
}

.elementBody{
    padding: 5vw;
}

.elementBodyPadding{
    padding: 2vw;
    border-radius: 30px;
}
#navigation{
    background-color: black !important;
}

@media (max-width: 990px) {
    .navbar-nav {
        height: 70vh !important;
        display: flex;
        justify-content: space-around;
        text-align: center;
    }

    .container button{
        margin-left: auto;
    }
}

@media (max-width: 990px) and (orientation: portrait){
    .navbar-nav {
        font-size: 4vh;
    }
}

@media (max-width: 990px) and (orientation: landscape){
    .navbar-nav {
        font-size: 4vw;
    }
}
.flexLine{
    display: flex;
    align-items: center;
    padding-top: 5vw;
    padding-bottom: 5vw;
}

#picture{
    width: 30%;
    padding-left: 5vw;
    padding-right: 5vw;
    align-items: center;
    justify-content: center;
    display: flex;
}

#me{
    width: 70%;
    height: auto;
    padding-right: 5vw;
    padding-left: 5vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
    align-items: center;
}

#me button{
    display: flex;
    width: 30%;
    justify-content: center;
    align-items: center;
    padding: 1vh;
    border: solid #0a3872d9 1px;
    border-radius: 100px;
    color: #0a3872d9;
    background-color: white;
    font-size: clamp(15px, 1vw, 100px);
    transition: color .3s ease-in-out;
    transition: background-color .3s ease-in-out;
}

#me button:hover{
    color: white;
    background-color: black;
    border: solid black 1px;
}

#me button svg{
    padding: 5px;
}

#desc{
    font-size: clamp(15px, 2vw, 100px);
}

span{
    padding-left: 10px;
    padding-right: 10px;
}

#separation{
    width: 100%;
    height: 2px;
    background-color: white;
    margin-top: 5vh;
    margin-bottom: 5vh;
}

#icons{
    position: relative;
    display: flex;
    justify-content: center;
    overflow: hidden;
}

#icons a{
    font-size: clamp(15px, 2vw, 120px);
    color: transparent;
    -webkit-animation: txt-appearance 0s 1.25s forwards;
            animation: txt-appearance 0s 1.25s forwards;
}

#icons svg{
    transition: color .3s ease-in-out;
}

#icons svg:hover{
    color: black !important;
    -webkit-filter: drop-shadow(0 0 1px black);
            filter: drop-shadow(0 0 1px black);
}

#icons a{
    margin-right: 25px;
    margin-left: 25px;
}

p {
    text-transform: uppercase;
    color: #ffffff;
    font-weight: 900;
    font-size: clamp(20px, 8vw, 120px);
    margin-bottom: 0 !important;
    line-height: 1;
}
p span {
    display: inline-block;
}

#first {
    font-weight: 300;
}

#second {
    color: black;
}

#icons::after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    background: white;
    -webkit-animation: slide-in 0.75s 0.3s ease-out forwards,
    slide-out 0.75s 1.3s ease-out forwards;
            animation: slide-in 0.75s 0.3s ease-out forwards,
    slide-out 0.75s 1.3s ease-out forwards;
}

@-webkit-keyframes slide-in {
    100% {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
}

@keyframes slide-in {
    100% {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
}
@-webkit-keyframes slide-out {
    100% {
        -webkit-transform: translateX(100%);
                transform: translateX(100%)
    }
}
@keyframes slide-out {
    100% {
        -webkit-transform: translateX(100%);
                transform: translateX(100%)
    }
}

@-webkit-keyframes txt-appearance {
    100% {
        color: #ffffff;
    }
}

@keyframes txt-appearance {
    100% {
        color: #ffffff;
    }
}

@media (max-width: 990px) and (orientation : portrait) {
    .flexLine{
        flex-direction: column;
        justify-content: flex-start;
    }

    #me{
        flex-grow: 1;
    }

    #me button{
        width: 80%;
    }

    #picture{
        flex-grow: 3;
        width: 40%;
        height: auto;
        padding-left: 0;
        padding-right: 0;
    }

    #icons{
        flex-wrap: wrap;
        justify-content: space-around;
        width: 80vw;
    }

    #icons svg {
        font-size: clamp(30px, 8vw, 50px);
        padding-right: 0;
        padding-left: 0;
    }

}

@media (max-width: 990px) and (orientation : landscape) {
    #icons svg {
        font-size: clamp(30px, 6vh, 50px);
    }

    #me{
        height: 100%;
    }

    #me button{
        width: 60%;
    }
}
#about{
    background-image: url(/cv-leo/static/media/fondAbout.a3ce3be0.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}

#cont{
    background-color: #0a3872d9 !important;
}
#myCareer{
    background-color: white;
    color: #0a3872d9;
}

#myCareer h1 {
    text-align: center;
}
#pictureElem{
    width: 8vw;
    margin-right: 2vw;
    border: solid 0.5px black;
    padding: 4px;
    height: 8vw;
    display: flex;
    align-items: center;
}

#element{
    display: flex;
}

#contentElem{
    flex: 1 1;
}

#separationElement{
    height: 0.5px;
    background-color: #c4c4c4;
    margin: 20px;
}

#titleElem{
    font-weight: 700;
    font-family: Montserrat,sans-serif;
    font-size: 25px;
}

.flex{
    display: flex;
    align-items: center;
}

.datelocation{
    width: 20vw;
    padding-left: 10px;
    padding-top: 10px;
}

#element svg{
    padding-right: 10px;
    color: #0a3872d9;
}

#descriptionElem{
    font-style: italic;
    padding-left: 10px;
    padding-bottom: 10px;
}

#flexDetails{
    display: flex;
}

@media (max-width: 990px){
    #pictureElem{
        width: 15vw;
        height: 15vw;
        margin-right: 0;
    }

    #contentElem{
        width: 100%;
    }

    #element{
        flex-direction: column;
        align-items: center;
    }

    .datelocation{
        width: inherit;
        flex-direction: row !important;
        align-items: center !important;
        width: 100%;
        justify-content: center;
    }

    .flex{
        flex-direction: column;
        align-items: flex-start;
    }

    #titleElem{
        text-align: center;
    }

    #descriptionElem{
        padding-top: 10px;
        padding-left: 0;
        text-align: center;
    }
}
.myCard{
    border: solid #0a3872d9 1px;
    margin-bottom: 5vh;
    color: #222;
}

.topicName{
    text-align: center;
    font-size: clamp(20px, 1vw, 100px);
    padding-bottom: 20px;
}

.spanName{
    padding-right: 0;
    padding-left: 0;
    padding-bottom: 5px;
    border-bottom: solid #0a3872d9 2px;
    color: black;
    text-transform: uppercase;

}

@media (max-width: 990px){
    .myCard{
        padding: 3vw;
    }
}
#footer{
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #bebebe;
    padding: 30px;
}

@media (max-width: 990px){
    #footer{
        padding: 10px;
        font-size: 10px;
    }
}
#containerContact{
    border: solid white 1px;
}

#contact h1{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: solid 1px white;
    margin-bottom: 20px;
}

#contact span{
    padding-left: 0;
}

#flexContact{
    display: flex;
    align-items: flex-start;
    flex-direction: column;
}

#flexContact div{
    margin-top: 10px;
    margin-bottom: 10px;
    margin-right: 10px;
}

#flexContact a{
    text-decoration: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 6px;
    border-radius: 30px;
    background-color: black;
    color: white;
    display: flex;
    align-items: center;
    transition: color .3s ease-in-out;
    transition: background-color .3s ease-in-out;
}

#flexContact a:hover{
    background-color: white;
    color: #0a3872;
}

#flexContact a svg, .flexLineContact svg{
    padding-right: 10px;
}

.flexLineContact{
    display: flex;
}

@media (max-width: 990px) and (orientation : portrait) {
    #flexContact{
        flex-direction: column;
    }

    #flexContact a{
        margin-top: 5px;
        margin-bottom: 5px;
    }
}
